* {
  margin: 0;
  padding: 0;
}

.parent {
  display: grid;
  grid-template-columns: 0.1fr 1fr;
  height: 100vh;
  .child1 {
    display: grid;
    grid-template-rows: 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr;
    height: 100vh;
    background-color: whilte;
    .logo {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgb(12, 12, 56);
    }
    .orders {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .whatshotIcon {
        text-align: center;
        .order-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
    .menu {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .MenuBookIcon {
        text-align: center;
        .menu-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
    .metrices {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .AssessmentIcon {
        text-align: center;
        .metrices-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif !important;
          font-size: 12px !important;
          margin: 5px;
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
    .discounts {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .AttachMoneyIcon {
        text-align: center;
        .discount-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
    .profile {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .AccountCircleIcon {
        text-align: center;
        .profile-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
    .help {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .HelpIcon {
        text-align: center;
        .help-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
  }
  .child2 {
    background-color: rgb(241, 241, 241);
    display: grid;
    grid-template-columns: 0.7fr 1.8fr;
    grid-template-rows: 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr;
    .mynav {
      grid-column: 1 / 3;
      width: 100%;
    }
    .search {
      grid-column: 1 / 2;
      grid-row: 2 / 9;
      text-align: center;
      .status {
        background-color: white;
        height: 94%;
        width: 85%;
        margin: 15px 0 25px auto;
        padding-top: 10px;
        .searchComp {
          margin-bottom: 10px;
        }
        .cardComp {
          ul {
            text-align: left;
            li {
              list-style: none;
              width: 100%;
              padding: 13px;
              &:hover {
                background-color: rgb(241, 241, 241);
              }
              p {
                span {
                  display: block;
                }
              }
            }
          }
        }
        .card {
          border: none;
          text-align: left;
          width: 100%;
          padding: 10px;
          background-color: white;
          border-radius: 2px;
          margin-top: 15px;
          margin-bottom: 15px;
          h6 {
            font-size: 12px;
          }
          h4 {
            color: orange;
          }
          &:hover {
            background-color: rgb(241, 241, 241);
            cursor: pointer;
          }
        }
      }
    }
    .checkout {
      grid-column: 2 / 3;
      grid-row: 2 / 9;
      .main {
        background-color: white;
        height: 94%;
        width: 85%;
        margin-top: 15px;
        margin-left: 15px;
        padding-top: 10px;
        text-align: center;
      }
    }
  }
}

.search {
  &__input {
    width: 80%;
    padding: 10px 30px;
    background-color: transparent;
    transition: transform 250ms ease-in-out;
    font-size: 12px;
    line-height: 18px;

    color: #575756;
    background-color: transparent;

    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-size: 18px 18px;
    background-position: 95% center;
    border-radius: 50px;
    border: 1px solid #575756;
    transition: all 250ms ease-in-out;
    backface-visibility: hidden;
    transform-style: preserve-3d;

    &::placeholder {
      color: color(#575756 a(0.8));
      text-transform: uppercase;
      letter-spacing: 1.5px;
    }

    &:hover,
    &:focus {
      padding: 10px 0;
      outline: 0;
      border: 1px solid transparent;
      border-bottom: 1px solid #575756;
      border-radius: 0;
      background-position: 100% center;
    }
  }
}
hr {
  width: 20%;
  border-top: 2px solid black;
}

.headingTop-Menu {
  h5 {
    padding-left: 14px;
  }
  text-align: left;
}
.SelectionCard {
  ul {
    padding-left: 15px;
    padding-right: 15px;
    text-align: left;
    li {
      width: 100%;
      display: flex;
      justify-content: space-between;
      &:hover {
        background-color: white;
      }
      p {
        span {
          display: block;
        }
      }
    }
  }
  .buttons {
    button {
      padding: 4px;
      padding-left: 10px;
      padding-right: 10px;
      border: none;
      margin-left: 10px;
      border-radius: 2px;
      &:hover {
        background-color: rgb(12, 12, 56);
        color: white;
      }
    }
  }
}
.MatricesMainComponent {
  background-color: rgb(241, 241, 241);
  display: grid;
  height: 87.5%;
  grid-template-columns: 1fr 1.3fr 1.9fr;
  section.days {
    grid-column: 1/2;
    height: 100%;
    padding-left: 35px;
    padding-top: 10px;
    .box1 {
      height: 85%;
      border-radius: 10px;
      background-color: white;
      margin-top: 15px;
      width: 100%;
      ul {
        li {
          display: flex;
          justify-content: space-between;
          list-style: none;
          padding: 14px;
          &:hover {
            background-color: rgb(241, 241, 241);
            cursor: pointer;
          }
        }
      }
    }
  }
  section.graph {
    grid-column: 2/3;
    display: grid;
    grid-template-rows: 1.9fr 1fr;
    height: 100%;
    margin-left: 15px;
    .top-insight {
      background-color: white;
      margin-top: 50px;
      width: 100%;
      border-radius: 10px;
      .top-section {
        text-align: center;
        padding-top: 30px;
        font-weight: 600;
      }
      .grid-container {
        padding-left: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        .left-side {
          grid-column: 1/2;
          padding: 35px;
          font-weight: 600;
          #large-font {
            font-size: 40px;
          }
          #small-font {
            font-size: 8px;
            color: gray;
          }
        }
        .right-side {
          height: 100%;
          width: 100%;
          grid-column: 2/3;
          display: flex;
          padding-left: 50px;
          align-items: center;
          .pic {
            height: 70%;
            width: 30px;
            background-color: rgb(223, 221, 221);
            .filled {
              margin-top: 43px;
              width: 100%;
              height: 80%;
              background: #ad5389; /* fallback for old browsers */
              background: -webkit-linear-gradient(
                to right,
                #3c1053,
                #ad5389
              ); /* Chrome 10-25, Safari 5.1-6 */
              background: linear-gradient(
                to right,
                #3c1053,
                #ad5389
              ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
            }
          }
        }
      }
    }

    section.operationalMatrices {
      grid-row: 2/3;
      height: 100%;
      width: 100%;
      padding-top: 10px;
      .heading {
        h6 {
          padding-left: 10px;
        }
      }
      .box3 {
        display: grid;
        grid-template-columns: 1.5fr 1fr;
        height: 62%;
        background-color: white;
        border-radius: 10px;
        .left {
          grid-column: 1/2;
          text-align: center;
          padding-top: 20px;
          height: 100%;
        }
        .right {
          grid-column: 2/3;
          font-size: 60px;
          height: 100%;
          display: flex;
          align-items: center;
        }
      }
    }
  }
  section.breakdown {
    grid-column: 3/4;
    display: grid;
    height: 100%;
    grid-template-rows: 1.9fr 1fr;
    section.top {
      grid-row: 1/2;
      height: 100%;
      .box4 {
        background-color: white;
        height: 84.5%;
        margin-left: 20px;
        margin-right: 40px;
        margin-top: 50px;
        border-radius: 10px;
        padding: 20px;
        .heading {
          font-family: "Roboto";
        }
        .grid-container {
          display: grid;
          grid-template-columns: 1fr 1fr;
          height: 90%;
          font-size: 14px;
          .first-col {
            grid-column: 1/2;
            display: flex;
            justify-content: center;
            .image {
              padding-bottom: 15px;
              font-size: 130px;
              color: rgb(2, 2, 160);
            }
          }
          .second-col {
            grid-column: 2/3;
            display: flex;
            justify-content: center;
            align-items: center;
            ul {
              text-align: center;
              width: 90%;
              li {
                color: white;
                border-radius: 10px;
                list-style: none;
                padding: 2px;
                margin-top: 7px;
              }
              li.first {
                background-color: rgb(248, 95, 120);
              }
              li.second {
                background-color: rgb(2, 2, 160);
              }
              li.third {
                background-color: rgb(223, 145, 0);
              }
            }
          }
        }
      }
    }
    section.extraSpace {
      margin-top: 36px;
      margin-left: 20px;
      grid-row: 2/3;
      display: grid;
      grid-template-columns: 1.5fr 1fr;
      height: 57%;
      width: 60%;
      background-color: white;
      border-radius: 10px;
      .left {
        grid-column: 1/2;
        text-align: center;
        padding-top: 20px;
        height: 100%;
      }
      .right {
        grid-column: 2/3;
        height: 100%;
        display: flex;
        align-items: center;
        font-size: 60px;
      }
    }
  }
}
